<template>
    <div id="container" class="container"></div>
    <div id="popup">
        <div class="close-btn"><img src="images/Mother_content_cross.png" /></div>
        <img src="images/Mother_content.jpg" alt="Popup Image" style="width:100%;">
    </div>
    <div id="welcome_popup">
        <img src="images/welcome/welocme.png" class="background" alt="Popup Image">
        <img src="images/welcome/btn_start.png" class="start_btn" alt="Popup Image">
    </div>
    <div id="passport"><img src="images/ui/btn_passport_off.png" /></div>
    <div id="passportStamp">
        <div class="stamp-close-btn"><img src="images/ui/btn_back.png" /></div>
        <div class="stamp-reward"><img src="images/ui/btn_off_reward.png" /></div>
        <img src="images/ui/bg_epassport.png" class="stamp-bg">
        <img src="images/ui/egg_1.png" class="egg-stamp">
    </div>
    <div id="reward_popup">
        <img src="images/reward/reward_msg.png" class="background" alt="Popup Image">
        <img src="images/reward/btn_cross.png" class="reward_cross_btn" alt="Popup Image">
    </div>
    <div id="map">
        <img src="images/ui/btn_big_map.png" />
        <img src="images/ui/btn_pointer.png" class="map_pointer" />
    </div>
    <div id="sound">
        <img src="images/ui/sound_off.png" />
    </div>
    <div id="carousel">
        <!-- <button @click="showMultiple">Show a group of pictures.</button> -->
        <vue-easy-lightbox
        :visible="visibleRef"
        :imgs="imgsRef"
        :index="indexRef"
        @hide="onHide"
        ></vue-easy-lightbox>
    </div>
</template>

<script>
import { ref, markRaw } from 'vue';
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader.js";
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

import VueEasyLightbox from 'vue-easy-lightbox'
//import { ParametricGeometry } from 'three/examples/jsm/geometries/ParametricGeometry.js';

let model;
let audio, reward_audio, reward_script_audio, welcome_audio;
let eggs = {};
let eggIcons = {};
let arrows = {};
let signs = {};
let signs2 = {};
let stampCard = {
    'egg1': false,
    'egg2': false,
    'egg3': false,
    'egg4': false,
    'egg5': false,
    'egg6': false,
    'egg7': false,
    'egg8': false,
    'egg9': false,
    'egg10': false,
    'collected': 0
}

export default {
    name: "ShopTour",
    components: { VueEasyLightbox },
    data() {
        return {
            isShowPopup: false,
            popupContent: "You've clicked on the 3D object!",
            envMaps: [], // This will store our  environment maps
            currentEnvMapIndex: 0, // Track the current environment map
            scene: markRaw(new THREE.Scene()), // Define the scene here to make it accessible
            renderer: null, // Define the renderer here to make it accessible
            controls: null, // Define the controls here to make them accessible
            camera: null, // Define the camera here to make it accessible
            raycaster: null, // Define the raycaster here to make it accessible
            mouse: null, // Define the mouse here to make it accessible
            model1: null, // Define the model here to make it accessible
            slickOptions: {
                dots: true,
                arrows: true
            },
            lightbox: null
        };
    },
    methods: {
        async initThreeJS() {
            const container = document.getElementById("container");
            // this.scene = new THREE.Scene();
            this.camera = new THREE.PerspectiveCamera(
                100,
                window.innerWidth / window.innerHeight,
                0.01,
                1000
            );
            this.renderer = new THREE.WebGLRenderer({ antialias: true });
            this.renderer.setSize(container.offsetWidth, container.offsetHeight);
            container.appendChild(this.renderer.domElement);

            this.controls = new OrbitControls(this.camera, this.renderer.domElement);
            // Disable zooming
            this.controls.enableZoom = false;

            this.camera.position.set(0, 0, 5); // Position the camera inside the sphere

            const lookAtPoint = new THREE.Vector3(0, 0, 0);
            const distance = -1 * this.camera.position.distanceTo(lookAtPoint);
            this.camera.position.x = lookAtPoint.x - distance;
            this.camera.position.z = lookAtPoint.z;

            this.camera.lookAt(lookAtPoint);

            this.controls.update();

            await this.loadHDRBackgrounds([
                'room/livingroom_back.hdr',
                'room/livingroom_front.hdr',
                'room/kitchen.hdr',
                'room/restroom_3.hdr',
                'room/toilet_room_4.hdr',
            ]);

            // Create flashing arrows
            for (let i = 0; i < 2; i++) {
                if (i === 0) {
                    this.createFlashingArrow(this.scene, new THREE.Vector3(-100, -20, 10)); // Middle arrow
                    continue;
                }
                if (i === 1) {
                    this.createFlashingArrow(this.scene, new THREE.Vector3(50, -20, -10)); // Middle arrow
                    continue;
                }
                //this.createFlashingArrow(scene, new THREE.Vector3(i * 5 - 5, 0, 0)); // Adjust positions as needed
            }

            // Add a clickable 3D object
            this.addInteractiveObject(this.scene);

            // Raycaster for click detection
            this.raycaster = new THREE.Raycaster();
            this.mouse = new THREE.Vector2();

            setTimeout(() => {
                this.animate();
            }, 3000);
            //this.animate();

            audio = new Audio('/assets/Mothership_background_music.mp3');
            audio.loop = true;
            audio.volume = 0.5;

            reward_script_audio = new Audio('/assets/ttsmaker-file-2024-5-15-17-57-16.mp3');
            reward_script_audio.volume = 0.8;

            reward_audio = new Audio('/assets/game-bonus-144751.mp3');
            reward_audio.volume = 1;
            
            welcome_audio = new Audio('/assets/welcome_speech.mp3');
            welcome_audio.volume = 0.8;

            document.getElementById('passport').addEventListener('click', () => {
                this.showPassport();
            });

            document.getElementById('sound').addEventListener('click', () => {
                this.playBackgroudSound();
            });

            let elements = document.querySelectorAll('li img.menu-img');
            elements.forEach(element => {
                element.addEventListener('click', this.handleMenuClick.bind(this));
            });
        },
        handleMenuClick(event) {
            let className = event.target.classList;
            if (className.contains('public')) {
                this.changeHDRBackground(2, 'kitchen_arrow_1');
            } else if (className.contains('kitchen')) {
                this.changeHDRBackground(2, 'living_back_arrow_1');
            } else if (className.contains('restroom')) {
                this.changeHDRBackground(3, 'living_back_arrow_3');
            } else if (className.contains('bathroom')) {
                this.changeHDRBackground(4, 'restroom_arrow_2');
            } else if (className.contains('argame')) {
                console.log('argame clicked');
            } else if (className.contains('homepage')) {
                //location.href = '/';
                this.changeHDRBackground(2, 'kitchen_arrow_1');

                document.getElementById('welcome_popup').style.display = 'block';

                this.playBackgroudSound();
                this.playWelcomeSound();

                // document.getElementsByClassName('start_btn')[0].addEventListener('click', () => {
                //     this.stopWelcomeSound();
                // });
            } else if (className.contains('passport')) {
                this.showPassport();
            }
        },
        onDocumentMouseDown(event) {
            event.preventDefault();

            this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
            this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

            this.raycaster.setFromCamera(this.mouse, this.camera);
            const intersects = this.raycaster.intersectObjects(this.scene.children, true);
            console.log('intersects.length : ' + intersects.length);
            for (let i = 0; i < intersects.length; i++) {
                

                let object = intersects[i].object;
                let tryObj = 5;

                while (object && !object.userData.isClickable && tryObj > 0) {
                    object = object.parent;
                    tryObj -= 1;
                }

                console.log('intersects[i].object.userData : ' + JSON.stringify(object.userData));
                console.log('object.visible : ' + object.visible);

                if (object.userData.isClickable && object.visible == true) {
                    if (object.userData.isInteractiveObject && object.userData.name == 'teapot') {
                        console.log('3D object clicked');
                        this.isShowPopup = true;
                        this.showPopup();
                        
                    }

                    if (object.userData.isInteractiveObject && object.userData.name == 'egg') {
                        let eggNum = object.userData.eggNum;
                        if(stampCard['egg' + eggNum] === true) {
                            console.log('Egg [' + eggNum + '] Already collected');
                        }else {
                            console.log('Egg [' + eggNum + '] is collected, thanks');
                            stampCard['egg' + eggNum] = true;
                            stampCard['collected'] += 1;

                            //stampCard['collected'] = 10;

                            if(stampCard['collected'] == 10) {
                                document.querySelector('.stamp-reward').classList.add('reward-active');
                                document.querySelector('.stamp-reward img').src = 'images/ui/btn_on_reward.png';

                                document.querySelector('.stamp-reward.reward-active').addEventListener('click', () => {
                                    document.getElementById('passportStamp').style.display = 'none';
                                    this.showReward();
                                });
                            }
                        }

                        this.showMultiple(eggNum);
                    }

                    if (object.userData.isInteractiveObject && object.userData.name == 'eggIcon') {
                        let iconNum = object.userData.iconNum;
                        console.log('Egg Icon [' + iconNum + '] Already clicked');
                        
                        this.showMultiple(iconNum);
                    }


                    if (object.userData.isInteractiveObjectGoToOtherRoom && object.userData.name != 'qrcode') {
                        const arrowKey = object.userData.name;
                        console.log('Image object clicked : ' + arrowKey);
                        let screenIdex = 0;
                        if(this.currentEnvMapIndex < 2) {
                            screenIdex = this.currentEnvMapIndex + 1;
                        }

                        this.changeHDRBackground(screenIdex, arrowKey);
                        this.showEgg(screenIdex, arrowKey);
                        
                    }
                }
            }
        },
        createFlashingArrow(position) {
            const tableMaterial = new THREE.MeshLambertMaterial({ color: 0x8B4513 }); // A brown color for tables
            const tableGeometry = new THREE.BoxGeometry(3, 0.2, 3); // Simplified table geometry
            const table = new THREE.Mesh(tableGeometry, tableMaterial);
            table.userData = { isClickable: true, isArrow: true };
            table.position.copy(position);
            this.scene.add(table);
        },
        async loadHDRBackgrounds(hdrPaths) {
            //const loader = new RGBELoader().setDataType(THREE.UnsignedByteType);
            const pmremGenerator = new THREE.PMREMGenerator(this.renderer);
            pmremGenerator.compileEquirectangularShader();

            // Store loaded and procaudio.loop = true;essed environment maps
            this.envMaps = []; 

            const promises = hdrPaths.map(path => 
                new Promise((resolve, reject) => {
                    console.log(path);
                    new RGBELoader()
                    .setPath('images/') // Assuming all HDR files are in the 'images/' directory
                    .load(path, function (texture) {
                        texture.mapping = THREE.EquirectangularReflectionMapping;
                        const envMap = texture;
                        //const envMap = pmremGenerator.fromEquirectangular(texture).texture;
                        texture.dispose(); // Free memory resources
                        resolve(envMap);
                    }, undefined, reject);
                })
            );

            Promise.all(promises).then(envMaps => {
                console.log('--------');
                this.envMaps = envMaps;
                console.log(this.envMaps);
                console.log(this.envMaps[0]);
                // Optionally set the first loaded environment map as the initial scene environment
                this.scene.background = this.envMaps[0];
                this.scene.environment = this.envMaps[0];
            }).finally(() => {
                pmremGenerator.dispose(); // Clean up resources
            });
        },
        changeHDRBackground(newIndex, arrowKey) {
            let currentEnvMap = '';
            let newEnvMap = '';

            switch(arrowKey) {
                case 'living_back_arrow_1':
                    //living room back -> kitchen 
                    currentEnvMap = this.envMaps[0];
                    newEnvMap = this.envMaps[2];
                    break;
                case 'living_back_arrow_2':
                    //living room back -> living room front
                    currentEnvMap = this.envMaps[0];
                    newEnvMap = this.envMaps[1];
                    break;
                case 'living_back_arrow_3':
                    //living room back -> restroom
                    currentEnvMap = this.envMaps[0];
                    newEnvMap = this.envMaps[3];
                    break;
                case 'living_front_arrow_1':
                    //living room front -> living room back
                    currentEnvMap = this.envMaps[1];
                    newEnvMap = this.envMaps[0];
                    break;
                case 'kitchen_arrow_1':
                    //kitchen -> living room back
                    currentEnvMap = this.envMaps[2];
                    newEnvMap = this.envMaps[0];
                    break;
                case 'restroom_arrow_1':
                    //restroom -> living room back
                    currentEnvMap = this.envMaps[3];
                    newEnvMap = this.envMaps[0];
                    break;
                case 'restroom_arrow_2':
                    //restroom -> bathroom
                    currentEnvMap = this.envMaps[3];
                    newEnvMap = this.envMaps[4];
                    break;
                case 'bathroom_arrow_1':
                    //bathroom -> restroom
                    currentEnvMap = this.envMaps[4];
                    newEnvMap = this.envMaps[3];
                    break;
            }

            
            //Implement a "fade" effect by interpolating the intensity
            let step = 0.0;
            const animateTransition = () => {
                step += 0.05; // Adjust step size for faster or slower transition
                if (step <= 1) {
                    //console.log('step : ' + step);
                    //this.scene.background = THREE.MathUtils.lerp(currentEnvMap, newEnvMap, step);
                    this.scene.environment = THREE.MathUtils.lerp(currentEnvMap, newEnvMap, step);
                    requestAnimationFrame(animateTransition);
                } else {
                    console.log('Finished animation');
                    this.scene.background = newEnvMap;
                    this.scene.environment = newEnvMap; // Ensure we end with the new map
                    this.currentEnvMapIndex = newIndex; // Update current index
                }
            };

            const arrlist = [
                'living_back_arrow_1', 'living_back_arrow_2', 
                'living_back_arrow_3', 'living_front_arrow_1', 
                'kitchen_arrow_1', 'restroom_arrow_1', 
                'restroom_arrow_2', 'bathroom_arrow_1'
            ];

            arrlist.forEach((item) => {
                arrows[item].visible = false;
                signs[item].visible = false;
            });

            switch(arrowKey) {
                case 'living_back_arrow_1':
                    arrows['kitchen_arrow_1'].visible = true;
                    signs['kitchen_arrow_1'].visible = true;

                    break;
                case 'living_back_arrow_2':
                    arrows['living_front_arrow_1'].visible = true;
                    signs['living_front_arrow_1'].visible = true;

                    break;
                
                case 'living_back_arrow_3':
                    arrows['restroom_arrow_1'].visible = true;
                    signs['restroom_arrow_1'].visible = true;
                    arrows['restroom_arrow_2'].visible = true;
                    signs['restroom_arrow_2'].visible = true;

                    this.updatePointer();

                    break;

                case 'living_front_arrow_1':
                    arrows['living_back_arrow_1'].visible = true;
                    signs['living_back_arrow_1'].visible = true;
                    arrows['living_back_arrow_2'].visible = true;
                    signs['living_back_arrow_2'].visible = true;
                    arrows['living_back_arrow_3'].visible = true;
                    signs['living_back_arrow_3'].visible = true;

                    break;
                
                case 'kitchen_arrow_1':
                    arrows['living_back_arrow_1'].visible = true;
                    signs['living_back_arrow_1'].visible = true;
                    arrows['living_back_arrow_2'].visible = true;
                    signs['living_back_arrow_2'].visible = true;
                    arrows['living_back_arrow_3'].visible = true;
                    signs['living_back_arrow_3'].visible = true;

                    break;
                case 'restroom_arrow_1':
                    arrows['living_back_arrow_1'].visible = true;
                    signs['living_back_arrow_1'].visible = true;
                    arrows['living_back_arrow_2'].visible = true;
                    signs['living_back_arrow_2'].visible = true;
                    arrows['living_back_arrow_3'].visible = true;
                    signs['living_back_arrow_3'].visible = true;

                    this.updatePointer();

                    break;
                case 'restroom_arrow_2':
                    arrows['bathroom_arrow_1'].visible = true;
                    signs['bathroom_arrow_1'].visible = true;

                    break;
                case 'bathroom_arrow_1':
                    arrows['restroom_arrow_1'].visible = true;
                    signs['restroom_arrow_1'].visible = true;
                    arrows['restroom_arrow_2'].visible = true;
                    signs['restroom_arrow_2'].visible = true;

                    break;
            }

            this.camera.position.set(0, 0, 5);
            const lookAtPoint = new THREE.Vector3(0, 0, 0);
            const distance = this.camera.position.distanceTo(lookAtPoint);
            this.camera.position.x = lookAtPoint.x - distance;
            this.camera.position.z = lookAtPoint.z;

            requestAnimationFrame(animateTransition);
        },
        addInteractiveObject() {
            const loader = new GLTFLoader();
            loader.load( 'images/reward/Reward/medal_gold2.glb',  ( gltf ) => {
                model = gltf.scene;
                model.position.set(0, 0, 0);
                model.rotation.y = - 1 * Math.PI / 2;
                model.scale.set(0.1, 0.1, 0.1);
                model.userData = { name: 'medal', isClickable: true, isInteractiveObject: true };

                model.lookAt(this.camera.position);
                model.visible = false;

                this.scene.add( model );
            }, undefined, function ( error ) {
                console.error( error );
            } );

            this.addArrow('living_back_arrow_1', '2_btn_kitchen_on.png', 1, true, 50, -20, 0);
            this.addArrow('living_back_arrow_2', '1_btn_public_area_on.png', 1, true, -25, -20, 0);
            this.addArrow('living_back_arrow_3', '3_btn_restroom_on.png', 1, true, 50, -20, 75);
            this.addArrow('living_front_arrow_1', '1_btn_public_area_on.png', 1, false, 60, -20, -2);
            this.addArrow('kitchen_arrow_1', '1_btn_public_area_on.png', 1, false, -50, -20, -2);
            this.addArrow('restroom_arrow_1', '1_btn_public_area_on.png', 1, false, 50, -20, 65);
            this.addArrow('restroom_arrow_2', '4_btn_bathroom_on.png', 1, false, -50, -20, 20);
            this.addArrow('bathroom_arrow_1', '3_btn_restroom_on.png', 1, false, 32, -30, -58);

            this.initialEgg('eggObj1', 1, true, -27, 15, 20, [-26, 0, 13], 0.6);
            this.initialEgg('eggObj2', 2, true, -5, -12, 40, [0, 0, 34], 0.8);
            this.initialEgg('eggObj3', 3, false, 5, -10, -40, [0, 0, -42], 0.9);
            this.initialEgg('eggObj4', 4, false, -5, -20, 40, [1, 0, 34], 0.6);
            this.initialEgg('eggObj5', 5, false, 29, -10, -30, [27, 0, -36], 0.9);
            this.initialEgg('eggObj6', 6, false, 27, -10, -20, [25, 0, -26], 0.9);
            this.initialEgg('eggObj7', 7, false, -27, -10,-60, [-22, 0, -66], 0.8);
            this.initialEgg('eggObj8', 8, false, -5, -12, 40, [0, 0, 34], 0.7);
            this.initialEgg('eggObj9', 9, false, -27, -10,-60, [-22, 0, -66], 0.6);
            this.initialEgg('eggObj10', 10, false, -5, -12, 40, [1, 0, 34], 0.7);

            this.addQRSign();
        },
        addQRSign() {
            const signloader = new THREE.TextureLoader();
            signloader.load('images/menu/5_btn_AR_game_on.png', (texture) => {
                // Create a sprite material with the loaded texture
                //const material = new THREE.SpriteMaterial({ map: texture, transparent: false });
                const geometry = new THREE.PlaneGeometry( 1, 1 );
                const material = new THREE.MeshBasicMaterial( {
                    map: texture,
                    transparent: true,
                    side: THREE.DoubleSide,
                } );
                const imageObj = new THREE.Mesh(geometry, material);
                imageObj.position.set(40, -22, 0);
                imageObj.lookAt(this.camera.position);
                imageObj.scale.set(25, 8, 10);
                imageObj.userData = { name: 'qrcode', isClickable: true, isInteractiveObjectGoToOtherRoom: true, signNum: 1, isArrow: false, x:1};

                imageObj.visible = false;

                this.scene.add(imageObj);
                signs2['qrcode'] = imageObj;
            }, undefined, function(err) {
                console.error('An error occurred loading the arrow texture:', err);
            });
        },
        addArrow(key, signImageName, arrowNum, show, posX, posY, posZ) {
            const arrowloader = new THREE.TextureLoader();
            arrowloader.load('images/Mother_arrow.png', (texture) => {
                // Create a sprite material with the loaded texture
                const material = new THREE.SpriteMaterial({ map: texture, transparent: true });
                const imageObj = new THREE.Sprite(material);
                imageObj.position.set(posX, posY, posZ);
                imageObj.scale.set(10, 8, 10);
                imageObj.userData = { name: key, isClickable: true, isInteractiveObjectGoToOtherRoom: true, arrowNum: arrowNum, isArrow: true};

                imageObj.visible = show;

                this.scene.add(imageObj);
                arrows[key] = imageObj;
            }, undefined, function(err) {
                console.error('An error occurred loading the arrow texture:', err);
            });

            const signloader = new THREE.TextureLoader();
            signloader.load('images/menu/'+signImageName, (texture) => {
                // Create a sprite material with the loaded texture
                //const material = new THREE.SpriteMaterial({ map: texture, transparent: false });
                const geometry = new THREE.PlaneGeometry( 1, 1 );
                const material = new THREE.MeshBasicMaterial( {
                    map: texture,
                    transparent: true,
                    side: THREE.DoubleSide,
                } );
                const imageObj = new THREE.Mesh(geometry, material);
                imageObj.position.set(posX, posY + 10, posZ);
                imageObj.lookAt(this.camera.position);
                imageObj.scale.set(25, 8, 10);
                imageObj.userData = { name: key, isClickable: true, isInteractiveObjectGoToOtherRoom: true, signNum: arrowNum, isArrow: false};

                imageObj.visible = show;

                this.scene.add(imageObj);
                signs[key] = imageObj;
            }, undefined, function(err) {
                console.error('An error occurred loading the arrow texture:', err);
            });
        },
        initialEgg(key, eggNum, show, posX, posY, posZ, iconPos, movementFactor = 0.1) {
            const eggloader = new GLTFLoader();
            eggloader.load( 'images/egg_bubble2.glb',  ( gltf ) => {
                const eggObj = gltf.scene;
                eggObj.position.set(posX, posY, posZ);
                eggObj.rotation.y = - 1 * Math.PI / 2;
                eggObj.scale.set(3, 3, 3);
                eggObj.userData = { name: 'egg', isClickable: true, isInteractiveObject: true, eggNum: eggNum, collected: false, movementFactor: movementFactor};

                eggObj.visible = show;

                this.scene.add( eggObj );
                eggs[key] = eggObj;
            }, undefined, function ( error ) {
                console.error( error );
            } );

            //eggIcons

            const iconloader = new THREE.TextureLoader();
            iconloader.load('images/ui/btn_discover.png', (texture) => {
                // Create a sprite material with the loaded texture
                //const material = new THREE.SpriteMaterial({ map: texture, transparent: false });
                const geometry = new THREE.PlaneGeometry( 1, 1 );
                const material = new THREE.MeshBasicMaterial( {
                    map: texture,
                    transparent: true,
                    side: THREE.DoubleSide,
                } );
                const imageObj = new THREE.Mesh(geometry, material);
                imageObj.position.set(iconPos[0], iconPos[1], iconPos[2]);
                imageObj.lookAt(this.camera.position);
                imageObj.scale.set(3, 3, 3);
                imageObj.userData = { name: 'eggIcon', isClickable: true, isInteractiveObject: true, iconNum: eggNum, isArrow: false};

                imageObj.visible = show;

                this.scene.add(imageObj);
                eggIcons[key] = imageObj;
            }, undefined, function(err) {
                console.error('An error occurred loading the arrow texture:', err);
            });
            
        },
        showEgg(newIndex, arrowKey) {
            console.log('showEgg newIndex : ' + newIndex);
            console.log('showEgg arrowKey : ' + arrowKey);

            //set all egg visible false
            for (let i = 1; i <= 10; i++) {
                eggs['eggObj'+i].visible = false;
                eggIcons['eggObj'+i].visible = false;
            }

            signs2['qrcode'].visible = false;

            switch(arrowKey) {
                case 'living_back_arrow_1':
                    eggs['eggObj4'].visible = true;
                    eggIcons['eggObj4'].visible = true;
                    eggs['eggObj5'].visible = true;
                    eggIcons['eggObj5'].visible = true;
                    break;
                case 'living_back_arrow_2':
                    eggs['eggObj3'].visible = true;
                    eggIcons['eggObj3'].visible = true;
                    break;
                case 'living_front_arrow_1':
                case 'kitchen_arrow_1':
                case 'restroom_arrow_1':
                    eggs['eggObj1'].visible = true;
                    eggIcons['eggObj1'].visible = true;
                    eggs['eggObj2'].visible = true;
                    eggIcons['eggObj2'].visible = true;
                    break;
                case 'living_back_arrow_3':
                case 'bathroom_arrow_1':
                    eggs['eggObj6'].visible = true;
                    eggIcons['eggObj6'].visible = true;
                    eggs['eggObj7'].visible = true;
                    eggIcons['eggObj7'].visible = true;
                    eggs['eggObj8'].visible = true;
                    eggIcons['eggObj8'].visible = true;
                    break;
                case 'restroom_arrow_2':
                    eggs['eggObj9'].visible = true;
                    eggIcons['eggObj9'].visible = true;
                    eggs['eggObj10'].visible = true;
                    eggIcons['eggObj10'].visible = true;

                    signs2['qrcode'].visible = false;
                    break;
            }
        },
        showPopup() {
            document.getElementById('popup').style.display = 'block';

            //document.querySelector('#popup textarea').focus();

            // Close popup when close button is clicked
            document.querySelector('.close-btn').addEventListener('mousedown', (event) => {
                event.preventDefault();
                event.stopPropagation();

                document.getElementById('popup').style.display = 'none';
                this.isShowPopup = false;
            });
        },
        showPassport() {
            document.getElementById('passportStamp').style.display = 'block';

            const collected = stampCard['collected'];
            console.log('collected : ' + collected);
            if(collected > 0) {
                document.querySelector('.egg-stamp').style.display = 'inline';
                document.querySelector('.egg-stamp').src = 'images/ui/egg_' + collected + '.png';
            }   

            // Close popup when close button is clicked
            document.querySelector('.stamp-close-btn').addEventListener('mousedown', (event) => {
                event.preventDefault();
                event.stopPropagation();

                document.getElementById('passportStamp').style.display = 'none';
                this.isShowPopup = false;
            });
        },
        showReward() {
            model.lookAt(this.camera.position);
            model.visible = true;

            this.playRewardSound();
            this.playRewardScriptSound();

            document.getElementById('reward_popup').style.display = 'block';

            // Close popup when close button is clicked
            document.querySelector('.reward_cross_btn').addEventListener('mousedown', (event) => {
                event.preventDefault();
                event.stopPropagation();

                document.getElementById('reward_popup').style.display = 'none';
                model.visible = false;
                this.isShowPopup = false;

                this.stopRewardSound();
                this.stopRewardScriptSound();
            });
        },
        updatePointer() {
            let element = document.querySelector('#map .map_pointer');
            if (element.classList.contains('first_floor')) {
                element.classList.remove('first_floor');
            } else {
                document.querySelector('#map .map_pointer').classList.add('first_floor');
            }
        },
        playBackgroudSound() {
            let element = document.querySelector('#sound img');
            if (element.src.includes('sound_on')) {
                element.src = 'images/ui/sound_off.png';
                audio.pause();
            } else {
                audio.currentTime = 0;
                element.src = 'images/ui/sound_on.png';
                audio.play();
            }
        },
        stopBackgroudSound() {
            audio.pause();
        },
        playRewardSound() {
            reward_audio.currentTime = 0;
            reward_audio.play();
        },
        stopRewardSound() {
            reward_audio.pause();
        },
        playRewardScriptSound() {
            reward_script_audio.currentTime = 0;
            reward_script_audio.play();
        },
        stopRewardScriptSound() {
            reward_script_audio.pause();
        },
        playWelcomeSound() {
            welcome_audio.currentTime = 0;
            welcome_audio.play();
        },
        stopWelcomeSound() {
            welcome_audio.pause();
        },
        animate() {
            requestAnimationFrame(this.animate);

            // Update controls
            this.controls.update();

            if (model) {
                const time = Date.now() * 0.002; // Current time in seconds
                model.position.y = 0.3 * Math.sin(time); // Move up and down over time

                // Rotate the model around the Y-axis continuously
                // model.rotation.y += 0.01;
            }

            for (let i = 1; i <= 10; i++) {
                const time = Date.now() * 0.002;
                eggs['eggObj'+i].position.y = 0.5 * Math.sin(time) * eggs['eggObj'+i].userData.movementFactor;
            }

            // Render the scene
            this.lightbox = this.renderer.render(this.scene, this.camera);
        }
    },
    mounted() {
        //this.scene = new THREE.Scene();
        this.initThreeJS();
        document.addEventListener('mousedown', this.onDocumentMouseDown.bind(this));

        document.getElementsByClassName('start_btn')[0].addEventListener('click', () => {
            document.getElementById('welcome_popup').style.display = 'none';
            this.stopWelcomeSound();
            this.playBackgroudSound();
        });
    },
    setup() {
        const visibleRef = ref(false)
        const indexRef = ref(0) // default 0
        const imgsRef = ref([])

        const onShow = () => {
            visibleRef.value = true
        }

        const showMultiple = (index) => {
            console.log('showMultiple - eggNum : ' + index);
            switch(index) {
                case 1:
                    imgsRef.value = [
                        '/images/egg_content/1/Mother_content_Page1_1.jpg',
                        '/images/egg_content/1/Mother_content_Page1_2.jpg',
                        '/images/egg_content/1/Mother_content_Page1_3.jpg',
                    ];
                    break;
                case 2:
                    imgsRef.value = [
                        '/images/egg_content/2/Mother_content_Page2_1.jpg',
                        '/images/egg_content/2/Mother_content_Page2_2.jpg',
                        '/images/egg_content/2/Mother_content_Page2_3.jpg',
                    ];
                    break;
                case 3:
                    imgsRef.value = [
                        '/images/egg_content/3/Mother_content_Page3_1.jpg',
                        '/images/egg_content/3/Mother_content_Page3_2.jpg',
                        '/images/egg_content/3/Mother_content_Page3_3.jpg',
                    ];
                    break;
                case 4:
                    imgsRef.value = [
                        '/images/egg_content/4/Mother_content_Page4_1.jpg',
                        '/images/egg_content/4/Mother_content_Page4_2.jpg',
                    ];
                    break;
                case 5:
                    imgsRef.value = [
                        '/images/egg_content/5/Mother_content_Page5_1.jpg',
                        '/images/egg_content/5/Mother_content_Page5_2.jpg',
                    ];
                    break;
                case 6:
                    imgsRef.value = [
                        '/images/egg_content/6/Mother_content_Page6_1.jpg',
                        '/images/egg_content/6/Mother_content_Page6_2.jpg',
                    ];
                    break;
                case 7:
                    imgsRef.value = [
                        '/images/egg_content/7/Mother_content_Page7_1.jpg',
                        '/images/egg_content/7/Mother_content_Page7_2.jpg',
                        '/images/egg_content/7/Mother_content_Page7_3.jpg',
                    ];
                    break;
                case 8:
                    imgsRef.value = [
                        '/images/egg_content/8/Mother_content_Page8_1.jpg',
                        '/images/egg_content/8/Mother_content_Page8_2.jpg',
                        '/images/egg_content/8/Mother_content_Page8_3.jpg',
                        '/images/egg_content/8/Mother_content_Page8_4.jpg',
                    ];
                    break;
                case 9:
                    imgsRef.value = [
                        '/images/egg_content/9/Mother_content_Page9_1.jpg',
                        '/images/egg_content/9/Mother_content_Page9_2.jpg',
                        '/images/egg_content/9/Mother_content_Page9_3.jpg',
                    ];
                    break;
                case 10:
                    imgsRef.value = [
                        '/images/egg_content/10/Mother_content_Page10_1.jpg',
                        '/images/egg_content/10/Mother_content_Page10_2.jpg',
                    ];
                    break;
                default:
                    imgsRef.value = [
                        'http://via.placeholder.com/350x150',
                        'http://via.placeholder.com/350x150'
                    ];
            }
            
            indexRef.value = 0 // index of imgList

            console.log('image loader index : ' + index)

            onShow()
        }

        const onHide = () => (visibleRef.value = false)

        return {
            visibleRef,
            indexRef,
            imgsRef,
            showMultiple,
            onHide
        }
    },
};
</script>

