<template>
    <div class="menu-container">
        <div class="menu-icon" id="menuIcon">
            <div class="icon"></div>
            <div class="menu" id="menu">
                <div class="menu-header">
                    <div class="menu-title"><img src="/images/menu/menu_txt.png"/></div>
                </div>
                <ul>
                    <li><a href="#"><img src="/images/menu/1_btn_public_area_off.png" class="public menu-img"/></a></li>
                    <li><a href="#"><img src="/images/menu/2_btn_kitchen_off.png" class="kitchen menu-img"/></a></li>
                    <li><a href="#"><img src="/images/menu/3_btn_restroom_off.png" class="restroom menu-img"/></a></li>
                    <li><a href="#"><img src="/images/menu/4_btn_bathroom_off.png" class="bathroom menu-img"/></a></li>
                    <li><a href="#"><img src="/images/menu/5_btn_AR_game_off.png" class="argame menu-img"/></a></li>
                    <li><a href="#"><img src="/images/menu/6_btn_Homepage_off.png" class="homepage menu-img"/></a></li>
                    <li><a href="#"><img src="/images/menu/7_btn_epassport_off.png" class="passport menu-img"/></a></li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'MenuPage',
  setup() {
    document.addEventListener('DOMContentLoaded', function () {
        //var menuContainer = document.getElementsByClassName('menu-container')[0];
        var menuIcon = document.getElementById('menuIcon');
        var menu = document.getElementById('menu');
        var isMouseOverMenu = false;

        menuIcon.addEventListener('mouseover', function () {
            // menuContainer.style.padding = '0px';
            menu.style.display = 'block';
            setTimeout(function () {
                menu.style.opacity = '1';
            }, 10); // Timeout to trigger transition
        });

        menuIcon.addEventListener('mouseout', function () {
            if (!isMouseOverMenu) {
                hideMenu();
            }
        });

        menu.addEventListener('mouseover', function () {
            isMouseOverMenu = true;
        });

        menu.addEventListener('mouseout', function () {
            isMouseOverMenu = false;
            hideMenu();
        });

        let elements = document.querySelectorAll('li img.menu-img');
        elements.forEach(element => {
            element.addEventListener('mouseover', function () {
                this.src = this.src.replace('off', 'on');
                console.log(this.classList + 'mouseover');
            });

            element.addEventListener('mouseout', function () {
                this.src = this.src.replace('on', 'off');
                console.log(this.classList + 'mouseout');
            });
            
        });

        function hideMenu() {
            menu.style.opacity = '0';
            setTimeout(function () {
                if (!isMouseOverMenu) {
                    menu.style.display = 'none';
                    // menuContainer.style.padding = '10px';
                }
            }, 500); // Match this duration with the transition duration
        }
    });
    // return {
    //   player: null
    // }
  }
}
</script>


