<template>
    <header>
      <link src="images/fav_icon.ico" />
    </header>
    <Home/>
    <Introduction/>
    <ShopTour/>
    <Menu/>
    <Warning/>
</template>

<script>
import Home from './components/Home'
import Introduction from './components/Introduction'
import ShopTour from './components/ShopTour'
import Menu from './components/Menu';
import Warning from './components/Warning';


let welcome_audio;

export default {
  name: 'App',
  components: {
    Home,
    Introduction,
    ShopTour,
    Menu,
    Warning
  },
  methods: {
    
  },
  mounted() {
    welcome_audio = new Audio('/assets/welcome_speech.mp3');
    welcome_audio.volume = 0.8;

    //check not in mobile
    if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
      document.getElementsByClassName('home')[0].style.display = 'none';
      document.getElementsByClassName('introduction')[0].style.display = 'none';
      document.getElementsByClassName('container')[0].style.display = 'none';
      document.getElementById('passport').style.display = 'none';
      document.getElementById('map').style.display = 'none';
      document.getElementById('sound').style.display = 'none';
      document.getElementsByClassName('menu-container')[0].style.display = 'none';

      document.getElementsByClassName('warning')[0].style.display = 'block';
    }else {
      document.getElementsByClassName('home')[0].style.display = 'block';
      document.getElementsByClassName('introduction')[0].style.display = 'none';
      document.getElementsByClassName('container')[0].style.display = 'none';
      document.getElementById('passport').style.display = 'none';
      document.getElementById('map').style.display = 'none';
      document.getElementById('sound').style.display = 'none';
      document.getElementsByClassName('menu-container')[0].style.display = 'none';
      document.getElementsByClassName('warning')[0].style.display = 'none';


      document.getElementsByClassName('enter_btn')[0].addEventListener('click', () => {
        if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
          alert('You are not on a desktop device.');
        }else {
          document.getElementsByClassName('home')[0].style.display = 'none';
          document.getElementsByClassName('introduction')[0].style.display = 'block'
          document.getElementsByClassName('container')[0].style.display = 'none'
          document.getElementById('passport').style.display = 'none';

          //document.getElementsByClassName('warning')[0].style.display = 'none'

          console.log('skip_btn2 clicked')
          if(document.querySelector('.vjs-big-play-button')) {
              document.querySelector('.vjs-big-play-button').click();
              console.log(document.querySelector('.vjs-big-play-button').innerHTML)
          }
        }
      });

      document.getElementsByClassName('skip_btn')[0].addEventListener('click', () => {
        console.log('skip_btn1 clicked')
        document.getElementsByClassName('home')[0].style.display = 'none';
        document.getElementsByClassName('introduction')[0].style.display = 'none';
        document.getElementsByClassName('container')[0].style.display = 'block';
        document.getElementById('welcome_popup').style.display = 'block';
        document.getElementById('passport').style.display = 'block';
        document.getElementById('map').style.display = 'block';
        document.getElementById('sound').style.display = 'block';
        document.getElementsByClassName('menu-container')[0].style.display = 'block';

        welcome_audio.currentTime = 0;
        welcome_audio.play();

        document.getElementsByClassName('start_btn')[0].addEventListener('click', () => {
          welcome_audio.pause();
        });
      });
    }


    

    // document.getElementsByClassName('home')[0].addEventListener('click', () => {
    //   document.getElementsByClassName('container')[0].style.display = 'none'
    //   document.getElementsByClassName('introduction')[0].style.display = 'block'
    // })
  },
  data: () => ({
    items: [
        { title: 'Click Me' },
        { title: 'Click Me' },
        { title: 'Click Me' },
        { title: 'Click Me 2' },
      ],
  }),
}
</script>

